  vertical-align: bottom;
  border-right: 0.12em solid #FF007A; /* pink caret */
  --type-ch: 37; /* approx char count */
  width: 0; /* start hidden */
  animation: typing 2.8s steps(var(--type-ch), end) 0.3s forwards, blink-caret 0.7s step-end infinite;
}

@keyframes typing {
  to { width: calc(var(--type-ch) * 1ch); }
}

@keyframes blink-caret {
  50% { border-color: transparent; }
}

@media (prefers-reduced-motion: reduce) {
  .main-blurb2 .typewriter {
    width: auto;
    animation: none;
